$("#toggle-menu").click(function() {
    if($("#sidebar").width() < 200) {
        $("#toggle-menu").css({
            "left": "280px"
        })
        $(".content-wrapper .page-header .page-title").css({
            "left": "310px"
        })
    } else {
        $("#toggle-menu").css({
            "left": "95px"
        })
        $(".content-wrapper .page-header .page-title").css({
            "left": "125px"
        })
    }
})
if ($("#donhang-chart").length) {
    async function fetchDonHang() {
        const response = await fetch('/api/chart/don-hang');
        const { data } = await response.json();
        let orderLabels = Object.keys(data);
        let orderDataSets = Object.values(data);

        let datasets = [
            {
                label: 'Delivered',
                data: orderDataSets,
                backgroundColor: '#392c70'
            }
        ];
        let donhangChart = $("#donhang-chart").get(0).getContext("2d");
        var currentChart = new Chart(donhangChart, {
            type: 'bar',
            data: {
                labels: orderLabels,
                datasets:  datasets
            },
            options: {
                responsive: true,
                maintainAspectRatio: true,
                layout: {
                    padding: {
                        left: 0,
                        right: 0,
                        top: 20,
                        bottom: 0
                    }
                },
                scales: {
                    yAxes: [{
                        gridLines: {
                            drawBorder: false,
                        },
                        ticks: {
                            stepSize: 250,
                            fontColor: "#686868"
                        }
                    }],
                    xAxes: [{
                        stacked: true,
                        ticks: {
                            beginAtZero: true,
                            fontColor: "#686868"
                        },
                        gridLines: {
                            display: false,
                        },
                        barPercentage: 0.4
                    }]
                },
                legend: {
                    display: false
                },
                elements: {
                    point: {
                        radius: 0
                    }
                },
                legendCallback: function(chart) {
                },
            }
        });
    }

    fetchDonHang();
}
